<template>
  <a-table
    :pagination="false"
    :rowKey="(v,i)=>i"
    :columns="table"
    :dataSource="tableData"
  >
    <a-table
      slot="expandedRowRender"
      :pagination="false"
      :rowKey="(v,i)=>i"
      :columns="table1"
      :dataSource="tableData"
    >
      <template slot="operation" slot-scope="text,v,i">
        <l-link>下载</l-link>
      </template>
    </a-table>
    <template slot="operation" slot-scope="text,v,i">
      <l-link>下载</l-link>
    </template>
  </a-table>
</template>
<script>
export default {
  data() {
    return {
      table: [
        {
          title: "序号",
          dataIndex: "name",
          scopedSlots: { customRender: "name" }
        },
        {
          title: "包装规格",
          dataIndex: "code",
          scopedSlots: { customRender: "code" }
        },
        {
          title: "长宽高(cm)",
          dataIndex: "id",
          scopedSlots: { customRender: "id" }
        },
        {
          title: "重量(kg)",
          dataIndex: "version",
          scopedSlots: { customRender: "version" }
        },
        {
          title: "操作",
          dataIndex: "operation",
          scopedSlots: { customRender: "operation" }
        }
      ],
      table1: [
        {
          title: "托盘型号",
          dataIndex: "name",
          scopedSlots: { customRender: "name" }
        },
        {
          title: "长宽高(cm)",
          dataIndex: "id",
          scopedSlots: { customRender: "id" }
        },
        {
          title: "载重(kg)",
          dataIndex: "version",
          scopedSlots: { customRender: "version" }
        },
        {
          title: "备注",
          dataIndex: "code",
          scopedSlots: { customRender: "code" }
        }
      ],
      tableData: []
    };
  },
  mounted() {
    this.getTable();
  },
  methods: {
    async getTable(current = this.current) {
      let data = Array(5)
        .fill({})
        .map((v, i) => {
          return {
            name: "name",
            code: "code",
            id: "id" + i,
            version: "version",
            state: "state"
          };
        });
      this.tableData = data;
    }
  }
};
</script>
