<template>
  <div class="check-box">
    <a-tabs hideAdd v-model="activeKey" type="editable-card" class="tabtilt" @edit="onEdit">
      <a-tab-pane v-for="(pane,i) in panes" :tab="pane.title" :key="i" :closable="!pane.close">
        <list ref="list" v-if="pane.type==0" @addTab="addTab"></list>
        <detail @closeTab="remove(i)" v-else-if="pane.type==1"></detail>
      </a-tab-pane>
    </a-tabs>
  </div>
</template>
<script>
import list from "./list";
import detail from "./detail";
export default {
  components: {
    list,
    detail
  },
  data() {
    return {
      activeKey: 0,
      panes: [{ title: "零部件审核", type: 0, close: true }]
    };
  },
  mounted() {},
  methods: {
    addTab(title = "", id = null, data, type = 1) {
      if (this.panes.some(v => id == v.id && type == v.type)) {
        this.activeKey = this.panes.findIndex(v => id == v.id);
      } else {
        this.panes.push({
          title,
          id,
          data,
          type
        });
        this.activeKey = this.panes.length - 1;
      }
    },
    onEdit(targetKey, action) {
      if (action == "remove") this[action](targetKey);
    },
    remove(targetKey) {
      let { panes, activeKey } = this;
      this.panes = panes.filter((v, i) => i != targetKey);
      this.activeKey = 0;
    }
  }
};
</script>
<style lang="less">
.check-box {
  height: 100%;
  overflow: hidden;
  min-width: 1100px;
  > .ant-tabs {
    height: 100%;
    padding-top: 50px;
    > .ant-tabs-bar {
      padding-top: 10px;
      margin-top: -50px;
      margin-bottom: 0;
    }
    .ant-tabs-content,
    .ant-tabs-tabpane {
      height: 100%;
    }
  }
}
</style>
