<template>
  <div class="check-dynamic-box">
    <div class="dynamic-title">审批动态</div>
    <div class="dynamic-main">
      <a-steps progressDot :current="1" class="steps" direction="vertical">
        <a-step v-for="(v,i) in steps" :key="i">
