            <div>{{v.step}} {{v.start}}</div>
            <div>
              <span class="result">{{v.result}}</span>
              <span class="time">{{v.time}}</span>
            </div>
          </div>
        </a-step>
      </a-steps>
      <a-button type="primary">已同意</a-button>
      <a-button type="primary" @click="agree">同意</a-button>
      <a-button @click="reject">驳回</a-button>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      steps: []
    };
  },
  mounted() {
    this.getDynamic();
  },
  methods: {
    getDynamic() {
      let data = [
        {
          step: "开始",
          result: "同意",
          time: "2019-08-09  18:00",
          start: "发起人:qearl"
        },
        {
          step: "初审",
          result: "同意",
          time: "2019-08-09  18:00",
          start: "主管B"
        },
        {
          step: "部门初审",
          result: "待审批",
          time: "2019-08-09  18:00",
          start: "主管B"
        },
        {
          step: "剩余审核",
          result: "待审批",
          time: "2019-08-09  18:00",
          start: "主管B"
        },
        {
          step: "结束",
          result: "",
          time: "",
          start: ""
        }
      ];
      this.steps = data;
    },
    agree(){
      this.$message.success('审核已同意')
    },
    reject() {
      this.$confirm({
        title: "确定驳回该条信息吗?",
        content:
          "",
        onOk() {
        },
        onCancel() {}
      });
    }
  }
};
</script>
<style lang="less">
.check-dynamic-box {
  .ant-steps {
    .ant-steps-item:not(:last-child) {
      .step-item {
        border-bottom: 1px solid #e4e4e4;
      }
    }
    .ant-steps-item-content {
      width: initial;
      .ant-steps-item-description {
        padding-bottom: 0;
      }
    }
  }
  .ant-btn + .ant-btn {
    margin-left: 10px;
  }
}
</style>
<style lang="less" scoped>
.check-dynamic-box {
  width: 350px;
  flex-shrink: 0;
  background-color: #fff;
  height: 100%;
  .dynamic-title {
    font-weight: bold;
    padding: 20px;
    padding-bottom: 10px;
    border-bottom: 1px solid #e4e4e4;
  }
  .dynamic-main {
    padding: 30px;
    .steps {
      margin-bottom: 50px;
      .step-item {
        padding: 10px 0;
        .result {
          font-weight: bold;
          font-size: 14px;
        }
      }
    }
