<template>
  <a-table
    class="table"
    :pagination="false"
    :rowKey="(v,i)=>i"
    :columns="table"
    :dataSource="tableData"
  >
    <template slot="operation" slot-scope="text,v,i">
      <l-link>下载</l-link>
    </template>
  </a-table>
</template>
<script>
export default {
  data() {
    return {
      table: [
        {
          title: "名称",
          dataIndex: "name",
          scopedSlots: { customRender: "name" }
        },
        {
          title: "编号",
          dataIndex: "code",
          scopedSlots: { customRender: "code" }
        },
        {
          title: "项目号",
          dataIndex: "id",
          scopedSlots: { customRender: "id" }
        },
        {
          title: "版本",
          dataIndex: "version",
          scopedSlots: { customRender: "version" }
        },
        {
          title: "图例",
          dataIndex: "img",
          scopedSlots: { customRender: "img" }
        },
        {
          title: "状态",
          dataIndex: "state",
          scopedSlots: { customRender: "state" }
        },
        {
          title: "操作",
          dataIndex: "operation",
          scopedSlots: { customRender: "operation" }
        }
      ],
      tableData: []
    };
  },
  mounted() {
    this.getTable();
  },
  methods: {
    async getTable(current = this.current) {
        let data = Array(5)
        .fill({})
        .map((v, i) => {
          return {
              name:'name',
              code:'code',
              id:'id'+i,
              version:'version',
              img:'img',
              state:'state'
          };
        });
      this.tableData = data;
    },
  }
};
</script>
<style lang="less" scoped>
</style>